import axios from "axios";

type sentenceType = {
  id: string,
  category: string,
  part: string,
  sentence: string,
  sentenceTrans: string,
}

type wordType = {
  id: string,
  category: string,
  part: string,
  word: string,
  plural: string,
  partOfSpeech: string,
  trans: string,
  sentence: string,
  sentenceTrans: string,
}

export default class EnglishData {
  static setWordData() {
    const sheetId = '10QM4n6jg93YevAhr-yS8SFp8VjaK_lv09VF_HfFxTYk';
    const sheetName = 'new_horizon_j1_word';
    const apiKey = 'AIzaSyD61LRLY0MGuJyIU9EQCb4KUDiFJRmxO1o';
    const words:wordType[] = [];
    return axios
    .get('https://sheets.googleapis.com/v4/spreadsheets/'+sheetId+'/values/'+sheetName+'?key='+apiKey)
    .then(response => {
      if (!Array.isArray(response.data.values)) {
        return words;
      }
      const responseWords = response.data.values;
      responseWords.shift();
      responseWords.forEach((w: any) => {
        words.push({
          id: w[0],
          category: w[1],
          part: w[2],
          word: w[3],
          plural: w[4],
          partOfSpeech: w[5],
          trans: w[6],
          sentence: w[7],
          sentenceTrans: w[8]
        })
      })
      sessionStorage.setItem('WORDS', JSON.stringify(words));
    })
    .catch(() => words);
  }

  static getWordData() {
    const storageWords = sessionStorage.getItem('WORDS');
    const words:wordType[] = [];
    if (storageWords != null && storageWords.length > 0) {
      JSON.parse(storageWords).forEach((w:any) => {
        words.push({
          id: w['id'],
          category: w['category'],
          part: w['part'],
          word: w['word'],
          plural: w['plural'],
          partOfSpeech: w['partOfSpeech'],
          trans: w['trans'],
          sentence: w['sentence'],
          sentenceTrans: w['sentenceTrans']
        })
      });
    }
    return words;
  }

  static getFavoriteWordData()
  {
    let words:wordType[] = [];
    let favorite = JSON.parse(localStorage.getItem('favorite') ?? '{}');
    const sWords = sessionStorage.getItem('WORDS');
    let storageWords:any = [];
    if (sWords != null && sWords.length > 0) {
      storageWords = JSON.parse(sWords);
    } else {
      return words;
    }
    if (Array.isArray(favorite)) {
      favorite.forEach(f => {
        if (f['category'] === 'Word') {
          const s:any = storageWords.filter((s:any) => s['id'] === f['id']);
          if (s.length > 0) {
            words.push(
              {
                id: s[0]['id'],
                category: s[0]['category'],
                part: s[0]['part'],
                word: s[0]['word'],
                plural: s[0]['plural'],
                partOfSpeech: s[0]['partOfSpeech'],
                trans: s[0]['trans'],
                sentence: s[0]['sentence'],
                sentenceTrans: s[0]['sentenceTrans']
              }
            );
          }
        }
      });
    }
    return words;
  }

  static getFirstPart() {
    return this.getWordData().reduce((val:string[], cur:any) => {
      if (!val.includes(cur.part)) {
        val.push(cur.part);
      }
      return val;
    }, [])[0];
  }

  static getParts() {
    return this.getWordData().reduce((val:string[], cur:any) => {
      if (!val.includes(cur.part)) {
        val.push(cur.part);
      }
      return val;
    }, []);  
  }

  static getSentences() {
    return this.getWordData().reduce((val:sentenceType[], cur:any) => {
      const w:sentenceType = {
        id: cur.id,
        category: cur.category,
        part: cur.part,
        sentence: cur.sentence, 
        sentenceTrans: cur.sentenceTrans
      };

      if (!val.some(v => 
        v.part === w.part && v.sentence === w.sentence)) {
        val.push(w);
      }
      return val;
    }, []);  
  }
}
