import React, { useState, useEffect } from 'react';
import { Container, List, ListItem, ListItemText, Slider } from '@material-ui/core';
import Paginate from '../components/Pagination';
import Grammer from '../components/GrammerWritingPage/Grammer';
import SideMenu from '../components/SideMenu';
import GrammerData from '../context/GrammerData';
import { useSwipeable } from 'react-swipeable';

const GrammerWritingPage:React.FC = () => {
  const firstPart = localStorage.getItem('grammerWritingCurrentPart') ?? GrammerData.getFirstPart();
  const wIndex: number = localStorage.getItem('grammerWritingWordIndex') ? Number(localStorage.getItem('grammerWritingWordIndex')) : 0;
  const [slides, setSlides] = useState(GrammerData.getSentences().filter(w => w.part === firstPart));
  const [wordIndex, setWordIndex] = useState<number>(wIndex);
  const [currentPart, setCurrentPart] = useState<string>('');
  const [isAnsComplated, setIsAnsComplated] = useState<boolean>(false);
  const [inputString, setInputString] = useState<string>('');
  const [visibleSentence, setVisibleSentence] = useState<boolean>(false);
  const [speechSpeedRate, setSpeechSpeedRate] = useState<number>(0.8);
  const [timer, setTimer] = useState<number>(0);

  const slidePrev = () => {
    if (wordIndex > 0) {
      const i:number = wordIndex - 1;
      setWordIndex(i);
      localStorage.setItem('grammerWritingWordIndex', i.toString());
      setIsAnsComplated(false);
      setVisibleSentence(false);
      setInputString('');
      setTimer(0);
    }
  }

  const slideNext = () => {
    if (wordIndex < slides.length-1) {
      const i:number = wordIndex + 1;
      setWordIndex(i);
      localStorage.setItem('grammerWritingWordIndex', i.toString());
      setIsAnsComplated(false);
      setVisibleSentence(false);
      setInputString('');
      setTimer(0);
    }
  }

  const changeCurrentPart = (val: string) => {
    if (currentPart !== val) {
      let s:any = [];
      if (val === 'favorite') {
        s = GrammerData.getFavoriteSentenceData();
      } else {
        s = GrammerData.getSentences().filter(w => w.part === val);
      }
      setSlides(s)
      setCurrentPart(val);
      localStorage.setItem('grammerWritingCurrentPart', val);
      setWordIndex(0);
      localStorage.setItem('grammerWritingWordIndex', '0');
      setIsAnsComplated(false);
      setInputString('');
      setTimer(0);
    }
  }

  const changeSpeechSpeedRate = (val: number) => {
    setSpeechSpeedRate(val);
  }

  const swipeHandles = useSwipeable({
    onSwipedLeft: (e) => {
      if (e.absX >= 140 ) {
        slideNext();
      }
    },
    onSwipedRight: (e) => {
      if (e.absX >= 140) {
        slidePrev();
      }
    },
    preventDefaultTouchmoveEvent: true,
    trackMouse: true
  });

  const changeInputString = (val: string) => {
    setInputString(val);
  }

  const changeIsAnsComplated = (val: boolean) => {
    setIsAnsComplated(val);
  }

  const changeVisibleSentence = (val: boolean) => {
    setVisibleSentence(val);
  }

  useEffect(() => {
    const setPart = async () => {
      let part:string = '';
      part = await localStorage.getItem('grammerWritingCurrentPart') ?? GrammerData.getFirstPart();
      await changeCurrentPart(part);
    };
    setPart();
    const id = setInterval(() => {
      setTimer(t => t + 1);
    }, 1000);
    return () => clearInterval(id);
  }, []);

  return (
    <>
      {
        currentPart !== ''
        &&
        <SideMenu
          categories={GrammerData.getCategories()}
          parts={GrammerData.getParts()}
          currentPart={currentPart}
          changeCurrentPart={(val) => changeCurrentPart(val)}
          speechSpeedRate={speechSpeedRate}
          changeSpeechSpeedRate={(val) => changeSpeechSpeedRate(val)}
        />
      }
      <Container>
        <Paginate
          index={wordIndex}
          maxPage={slides.length}
          onClickPrev={slidePrev}
          onClickNext={slideNext}
        />
        <hr/>
        {
          slides.length == 0
          &&
          <p>no data</p>
        }
        {
          slides.length > 0
          &&
          <>
            <div {...swipeHandles}>
              <Grammer
                id={slides[wordIndex].id}
                currentPart={currentPart}
                sentence={slides[wordIndex].sentence.replaceAll('  ', ' ')}
                visibleSentence={visibleSentence}
                inputString={inputString}
                translate={slides[wordIndex].sentenceTrans}
                speechSpeedRate={speechSpeedRate}
                isAnsComplated={isAnsComplated}
                changeInputString={(v) => changeInputString(v)}
                changeIsAnsComplated={(v) => changeIsAnsComplated(v)}
                changeVisibleSentence={(v) => changeVisibleSentence(v)}
              />
            </div>
            <List component="nav" aria-label="thirdry speechSpeed">
              <ListItem>
                <ListItemText primary="voice speed"/>
                <Slider
                  min={0.1}
                  max={1}
                  step={0.1}
                  value={speechSpeedRate}
                  aria-labelledby="speech-speed-rate"
                  valueLabelDisplay="auto"
                  onChange={(e, v) => {
                    let rate:number = speechSpeedRate;
                    if (typeof(v) ===  'number') {
                      rate = v;
                    }
                    setSpeechSpeedRate(rate);
                  }}
                />
              </ListItem>
            </List>
          </>
        }
        {
          timer >= 20
          &&
          <p>time over</p>
        }
        {
          timer < 20
          &&
          <p>Time: {20-timer}</p>
        }
      </Container>
    </>
  );
}

export default GrammerWritingPage;