import React from 'react';
import { Container, Button, Grid, Typography } from '@material-ui/core';
import VolumeUpIcon from '@material-ui/icons/VolumeUp';
import EnglishVoice from '../../lib/EnglishVoice';

interface GrammerProps {
  answer: string[];
  stockWords: string[];
  ansWords: string[];
  sentence: string;
  translate: string;
  isAnsComplated: boolean;
  speechSpeedRate: number;
  moveAnsWords: (w:string, i:number) => void;
  moveStockWords: (w:string, i:number) => void;
}

const Grammer: React.FC<GrammerProps> = props => {
  const stockWords = props.stockWords;
  const ansWords = props.ansWords;
  const lastChar:string = props.sentence.substr(-1);

  const buttonStyle: React.CSSProperties = {
    display: 'inline-block',
    minWidth: '20px',
    height: '40px',
    textTransform: 'lowercase',
    margin: '5px',
  }

  const wordsStyle: React.CSSProperties = {
    minHeight: '30vh',
  }

  const style: React.CSSProperties = {
    display: 'inline-block',
    width: '100%',
    height: '20%',
  }

  return (
    <>
      <Container maxWidth="sm">
        <Grid container>
          <Typography variant='subtitle1'>
            {props.translate}
          </Typography>
        </Grid>
      </Container>
      <Container maxWidth="sm" style={wordsStyle}>
        <Grid container>
          {
            ansWords.map((w, index) => {
              return (
                <Button
                  style={buttonStyle}
                  color={props.isAnsComplated ? 'default' : 'secondary'}
                  variant={props.isAnsComplated ? 'outlined' : 'contained'}
                  size="small"
                  onClick={() => props.moveStockWords(w, index)}
                  key={index}
                >
                  {w}
                </Button>
              )
            })
          }
          <Button
            style={buttonStyle}
            color="default"
            variant="text"
            size="small"
            key="lastChar"
          >
            {lastChar}
          </Button>
        </Grid>
      </Container>
      <Container maxWidth="sm" style={wordsStyle}>
        <Grid container>
          {
            stockWords.map((w, index) => {
              return (
                <Button
                  style={buttonStyle}
                  color="primary"
                  variant="contained"
                  size="small"
                  onClick={() => props.moveAnsWords(w, index)}
                  key={index}
                >
                  {w}
                </Button>
              )
            })
          }
        </Grid>
      </Container>
      <hr />
      <Container maxWidth="sm" style={wordsStyle}>
        <Grid container>
          <Button
            style={style}
            variant="outlined"
            onClick={() => {
              EnglishVoice.speech(props.sentence, 'en-US', props.speechSpeedRate);
            }}
          >
            <VolumeUpIcon
              fontSize='small'
            />
          </Button>
        </Grid>
      </Container>
    </>
  );
}

export default Grammer;