import * as React from 'react';
import { Container, Button, Grid, Typography, TextField, Avatar } from '@material-ui/core';
import EnglishVoice from '../../lib/EnglishVoice';
import VolumeUpIcon from '@material-ui/icons/VolumeUp';
import TranslateIcon from '@material-ui/icons/Translate';
import 'fontsource-roboto';

interface WordProps {
    word: string;
    partOfSpeech: string;
    trans: string;
    visibleWord: string;
    isInputWordCompleted: boolean;
    inputWordString:string;
    onClick: () => void;
    changeInputWordCompleted: (val:boolean) => void;
    changeInputWord: (val:string) => void;    
}

class Word extends React.Component<WordProps, {}>{
  render () {
    const style: React.CSSProperties = {
      display: 'inline-block',
      width: '100%',
      height: '20%',
    }
    
    const partOfSpeech = this.props.partOfSpeech.length ? this.props.partOfSpeech : '';

    return (
      <div className="word-wrap">
        <Container maxWidth="sm">
          <Grid container>
            <Grid item xs={10}>
              <Typography variant='subtitle1'>
                {this.props.word}
              </Typography>
            </Grid>
            <Grid item xs={2}>
              <Avatar
                variant='circular'
                component='span'
              >
                {partOfSpeech.substr(0,1)}
              </Avatar>
            </Grid>
          </Grid>
          <Typography variant='h5' className={this.props.visibleWord}>
            {this.props.trans}
          </Typography>
          <br/>
          <Grid
            container
            direction="row"
            spacing={5}
            justify="center"
            alignItems="center"
          >
            <Grid item xs={5}>
              <Button
                style={style}
                color="primary"
                variant="outlined"
                onClick={() => this.props.onClick()}
                >
                  <TranslateIcon/>
              </Button>
            </Grid>
            <Grid item xs={5}>
              <Button
                style={style}
                variant="outlined"
                onClick={() => {
                  EnglishVoice.speech(this.props.word, 'en-US', 1);
                }}
              >
                <VolumeUpIcon/>
              </Button>
            </Grid>
          </Grid>
        </Container>
      </div>
    );
  }
}

export default Word;