import React, { useState, useEffect } from 'react';
import { Button, Grid } from '@material-ui/core';
import StarIcon from '@material-ui/icons/Star';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import 'fontsource-roboto';

interface FavoriteProps {
    category: 'Grammer'|'Word'|'GrammerWriting';
    part: string;
    id: string;
}

export default function Favorite(props:FavoriteProps) {
    const [isFavorite, setIsFavorite] = useState<boolean>(false);
    const [id, setId] = useState<string>(props.id);

    const checkIsFavorite = async () => {
        let favorite:any = [];
        favorite = await JSON.parse(localStorage.getItem('favorite') ?? '{}');
        if (Array.isArray(favorite)) {
            return favorite.filter(f => f['category'] === props.category && f['id'] === props.id).length > 0;
        } else {
            return false;
        }
    }

    const setFavorite = async () => {
        let favorite = await JSON.parse(localStorage.getItem('favorite') ?? '{}');
        if (Array.isArray(favorite)) {
            await checkIsFavorite()
            .then((isF:any) => {
                if (isF) {
                    favorite = favorite.filter((f:any) => f['category'] !== props.category || f['id'] !== props.id);
                    setIsFavorite(false);
                } else {
                    favorite = favorite.concat(props);
                    setIsFavorite(true);
                }
                localStorage.setItem('favorite', JSON.stringify(favorite));
            });
        } else {
            localStorage.setItem('favorite', JSON.stringify([props]));
            setIsFavorite(true);
        }
    }

    useEffect(() => {
        checkIsFavorite()
        .then((isFa:boolean) => {
            setIsFavorite(isFa);
        })
    })

    return (
        <Button
            variant="contained"
            color="primary"
            size="small"
            onClick={setFavorite}
        >
        {
            isFavorite ? <StarIcon /> : <StarBorderIcon />
        }
        </Button>
    )

}
