import * as React from 'react';
import { Container, Button, Grid, Typography, TextField, Avatar } from '@material-ui/core';
import EnglishVoice from '../../lib/EnglishVoice';
import VolumeUpIcon from '@material-ui/icons/VolumeUp';
import TranslateIcon from '@material-ui/icons/Translate';
import 'fontsource-roboto';

interface PluralProps {
    plural: string;
    partOfSpeech: string;
    trans: string;
    visiblePlural: string;
    isInputPluralCompleted: boolean;
    inputPluralString:string;
    onClick: () => void;
    changeInputPluralCompleted: (val:boolean) => void;
    changeInputPlural: (val:string) => void;    
}

class Plural extends React.Component<PluralProps, {}>{
  render () {
    const style: React.CSSProperties = {
      display: 'inline-block',
      width: '100%',
      height: '20%',
    }
    
    const partOfSpeech = this.props.partOfSpeech.length ? this.props.partOfSpeech : '';

    return (
      <div className="word-wrap">
        <Container maxWidth="sm">
          <Typography variant='h4' className={this.props.visiblePlural}>
            {this.props.plural}
          </Typography>
          <br/>
          <Grid container direction="row">
            <TextField
              error={!this.props.isInputPluralCompleted}
              label= {this.props.isInputPluralCompleted ? "OK" : "input plural"}
              fullWidth={true}
              value={this.props.inputPluralString}
              variant="outlined"
              onChange={(o) => 
                {
                  this.props.changeInputPlural(o.target.value.toLowerCase());
                  if (this.props.plural.toLowerCase() === o.target.value.toLowerCase()) {
                    this.props.changeInputPluralCompleted(true);
                    this.props.onClick();
                    EnglishVoice.speech('OK. ' + this.props.plural, 'en-US', 1);
                  } else {
                    this.props.changeInputPluralCompleted(false);
                  }
                }
              }
            />
          </Grid>
          <br/>
          <Grid
            container
            direction="row"
            spacing={5}
            justify="center"
            alignItems="center"
          >
            <Grid item xs={5}>
              <Button
                style={style}
                color="primary"
                variant="outlined"
                onClick={() => this.props.onClick()}
                >
                  <TranslateIcon/>
              </Button>
            </Grid>
            <Grid item xs={5}>
              <Button
                style={style}
                variant="outlined"
                onClick={() => {
                  EnglishVoice.speech(this.props.plural, 'en-US', 1);
                }}
              >
                <VolumeUpIcon/>
              </Button>
            </Grid>
          </Grid>
        </Container>
      </div>
    );
  }
}

export default Plural;