import React, { useState, useEffect } from 'react';
import clsx from 'clsx';
import { Drawer, IconButton, List, ListItem, Collapse, Slider } from '@material-ui/core';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import { AppBar, Toolbar } from '@material-ui/core';
import { ListItemText, ListItemIcon } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/Close';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
import Divider from '@material-ui/core/Divider';
import { VerticalSplitSharp } from '@material-ui/icons';

type categoryType = {
  category: string;
  parts: string[];
}
interface SideMenuProps {
  categories: categoryType[]|null;
  parts: string[];
  currentPart: string;
  changeCurrentPart: (val:string) => void;
  speechSpeedRate: number;
  changeSpeechSpeedRate: (val:number) => void;
}

const SideMenu: React.FC<SideMenuProps> = props => {
  const [menuToggle, setMenuToggle] = useState(false);
  const [listOpen, setListOpen] = useState<boolean[]>([]);

  const toggleDrawer = () => {
    setMenuToggle(!menuToggle);
  };

  const categories:categoryType[] | null = props.categories;
  const parts = props.parts;
  const drawerWidth = '70vw';

  useEffect(() => {
    if (categories) {
      let vals:boolean[] = [];
      categories.forEach(c => {
        if (c.parts.includes(props.currentPart)) {
          vals.push(true);
        } else {
          vals.push(false);
        }
      })
      setListOpen(vals);
    }
  },[]);

  const useStyles = makeStyles((theme: Theme) => 
    createStyles({
      header: {
        height:'4vh',
        backgroundColor: '#7A7A7A',
        marginBottom: '1vh'
      },
      open: {
        width: drawerWidth,
      },
      close: {
        width:'100%',
      },
      closeIcon: {
        marginTop: '16px',
        marginLeft: '16px',
      },
      hide: {
        display: 'none',
      },
      list: {
        width: drawerWidth,
      },
      categories: {
        backgroundColor: '#333',
      },
      parts: {
        paddingLeft: '2em',
      }
    })
  );

  const changeCategoryMenu = ((index:number|null) => {
    let vals:boolean[] = [];
    listOpen.forEach(c => {
      vals.push(false);
    })
    if (index !== null) {
      vals.splice(index, 1, true);
    }
    setListOpen(vals);
  });

  const classes = useStyles();

  return ( 
    <>
      <Toolbar
        className={`${menuToggle ? classes.open : classes.close} ${classes.header}` }
      >
        <Typography
          variant="h6"
          noWrap
          style={{flexGrow: 1, textAlign:'center'}}
        >
          {props.currentPart}    
        </Typography>
        <IconButton
          color="inherit"
          className={menuToggle ? classes.hide : ''}
          aria-label="open drawer"
          edge="end"
          onClick={() => toggleDrawer()}
        >
          <MenuIcon />
        </IconButton>
      </Toolbar>
      <Drawer
        anchor='right'
        open={menuToggle}
        onClose={() => setMenuToggle(false)}
      >
        <div className={classes.list}>
          <CloseIcon
            className={classes.closeIcon}
            onClick={() => setMenuToggle(false)}
          />
          <List key={'menuList'} component="nav" aria-label="secondary part">
            {
              categories
              &&
              categories.map((c, index) => {
                return (
                  <div key={'categoryWrapper'+index}>
                    <ListItem
                      key={'category'+index}
                      className={classes.categories}
                      button
                      onClick={() => {
                        if (listOpen[index]) {
                          changeCategoryMenu(null);
                        } else {
                          changeCategoryMenu(index);
                        }
                      }}
                    >
                      <ListItemText key={'categoryText'} primary={c.category}/>
                      {listOpen[index] ? <ExpandLess /> : <ExpandMore />}
                    </ListItem>
                    <Collapse key={'subMenu'+index} in={listOpen[index]} timeout="auto" unmountOnExit>
                      <List key={'subMenuList'+index} component="div" disablePadding>
                        {
                          c.parts.map((p, i) => 
                            <ListItem 
                              key={'subMenu'+index+p+i}
                              button
                              className={classes.parts}
                              onClick={() => {
                                props.changeCurrentPart(p);
                                setMenuToggle(false);
                                changeCategoryMenu(index);
                              }}
                                        
                            >
                              <ListItemText key={'subMenuText'+index+p+i} primary={p.replaceAll(c.category, '').trim()}/>
                              <ListItemIcon key={'subMenuIcon'+index+p+i}>
                                {
                                  p === props.currentPart
                                  &&
                                  <CheckIcon />
                                }
                              </ListItemIcon>
                            </ListItem>
                          )
                        }
                      </List>
                    </Collapse>
                  </div>
                )
              })
            }
            {
              !categories && parts
              &&
              parts.map(p => {
                return (
                  <ListItem 
                    button
                    onClick={() => {
                      props.changeCurrentPart(p);
                      setMenuToggle(false);
                    }}
                    key={p}
                  >
                    <ListItemText primary={p}/>
                    <ListItemIcon>
                      {
                        p === props.currentPart
                        &&
                        <CheckIcon />
                      }
                    </ListItemIcon>
                  </ListItem>
                )
              })
            }
            <ListItem
              button
              onClick={() => {
                props.changeCurrentPart('favorite');
                setMenuToggle(false);
              }}
              key={'favorite'}
            >
              <ListItemText primary="favorite"/>
            </ListItem>
          </List>
          <Divider />
          <List key={'voiceSpeedList'} component="nav" aria-label="thirdry speechSpeed">
            <ListItem key={'voiceSpeedListItem'}>
              <ListItemText primary="voice speed"/>
              <Slider
                min={0.1}
                max={1}
                step={0.1}
                value={props.speechSpeedRate}
                aria-labelledby="speech-speed-rate"
                valueLabelDisplay="auto"
                onChange={(e, v) => {
                  let rate:number = props.speechSpeedRate;
                  if (typeof(v) ===  'number') {
                    rate = v;
                  }
                  props.changeSpeechSpeedRate(rate);
                }}
              />
            </ListItem>
          </List>
        </div>
      </Drawer>
    </>
  );
}

export default SideMenu;