import React from 'react';
import { BottomNavigation, BottomNavigationAction } from '@material-ui/core';
import TextFieldsIcon from '@material-ui/icons/TextFields';
import TextFormatIcon from '@material-ui/icons/TextFormat';
import CreateIcon from '@material-ui/icons/Create';

interface GlobalMenuProps {
  currentPage: string,
  changeCurrentPage: (val:pageNames) => void
}

const GlobalMenu:React.FC<GlobalMenuProps> = props => {

  const style:React.CSSProperties = {
    position:'fixed',
    bottom:0,
    width: '100%',
  }

  return (
    <BottomNavigation
      showLabels
      style={style}
      value={props.currentPage}
      onChange={(event, newValue:pageNames) => {
        props.changeCurrentPage(newValue);
        localStorage.setItem('currentPage', newValue);
      }}
    >
      <BottomNavigationAction label='Word' value='WordPage' icon={<TextFormatIcon />} />
      <BottomNavigationAction label='Grammer' value='GrammerPage'icon={<TextFieldsIcon />} />
      <BottomNavigationAction label='GrammerWrite' value='GrammerWritingPage'icon={<TextFieldsIcon />} />
      <BottomNavigationAction label='Test' value='GrammerWritingTestPage'icon={<CreateIcon />} />
    </BottomNavigation>
  );
}

export default GlobalMenu;