import axios from "axios";

type sentenceType = {
  id: string,
  category: string,
  part: string,
  sentence: string,
  sentenceTrans: string,
  dummyWord: string,
}

type grammerType = {
  id: string,
  category: string,
  part: string,
  sentence: string,
  sentenceTrans: string,
  dummyWord: string,
}

type categoryType = {
  category: string;
  parts: string[];
}

export default class GrammerData {
  static setGrammerData() {
    const sheetId = '10QM4n6jg93YevAhr-yS8SFp8VjaK_lv09VF_HfFxTYk';
    const sheetName = 'csv_new_horizon_j1';
    const apiKey = 'AIzaSyD61LRLY0MGuJyIU9EQCb4KUDiFJRmxO1o';
    const sentences:grammerType[] = [];
    return axios
    .get('https://sheets.googleapis.com/v4/spreadsheets/'+sheetId+'/values/'+sheetName+'?key='+apiKey)
    .then(response => {
      if (!Array.isArray(response.data.values)) {
        return sentences;
      }
      const responseWords = response.data.values;
      responseWords.shift();
      responseWords.forEach((w: any) => {
        sentences.push({
          id: w[0],
          category: w[1],
          part: w[2],
          sentence: w[3],
          sentenceTrans: w[4],
          dummyWord: w[5],
        })
      })
      sessionStorage.setItem('SENTENCES', JSON.stringify(sentences));
    })
    .catch(() => sentences);
  }

  static getGrammerData() {
    const storageSentences = sessionStorage.getItem('SENTENCES');
    const sentences:grammerType[] = [];
    if (storageSentences != null && storageSentences.length > 0) {
      JSON.parse(storageSentences).forEach((s:any) => {
        sentences.push({
          id: s['id'],
          category: s['category'],
          part: s['part'],
          sentence: s['sentence'],
          sentenceTrans: s['sentenceTrans'],
          dummyWord: s['dummyWord'],
        })
      });
    }
    return sentences;
  }

  static getFavoriteSentenceData()
  {
    let sentences:sentenceType[] = [];
    let favorite = JSON.parse(localStorage.getItem('favorite') ?? '{}');
    const sSentences = sessionStorage.getItem('SENTENCES');
    let storageSentences:any = [];
    if (sSentences != null && sSentences.length > 0) {
      storageSentences = JSON.parse(sSentences);
    } else {
      return sentences;
    }
    if (Array.isArray(favorite)) {
      favorite.forEach(f => {
        if (f['category'] === 'Grammer') {
          const s:any = storageSentences.filter((s:any) => s['id'] === f['id']);
          if (s.length > 0) {
            const d = s[0];
            sentences.push(
              {
                id: d['id'],
                category: d['category'],
                part: d['part'],
                sentence: d['sentence'],
                sentenceTrans: d['sentenceTrans'],
                dummyWord: d['dummyWord'],
              }
            );
          }
        }
      });
    }
    return sentences;
  }

  static getFirstPart() {
    return this.getGrammerData().reduce((val:string[], cur) => {
      if (!val.includes(cur.part)) {
        val.push(cur.part);
      }
      return val;
    }, [])[0];  
  }

  static getParts() {
    return this.getGrammerData().reduce((val:string[], cur) => {
      if (!val.includes(cur.part)) {
        val.push(cur.part);
      }
      return val;
    }, []);  
  }

  static getCategories() {
    const categories = this.getGrammerData().reduce((val:string[], cur) => {
      if (!val.includes(cur.category)) {
        val.push(cur.category);
      }
      return val;
    }, []);

    return categories.map(c => {
      const parts = this.getGrammerData().reduce((val:string[], cur) => {
//        const part = cur.part.replaceAll(c , '');
        const part = cur.part;
        if (!val.includes(part) && c === cur.category) {
          val.push(part);
        }
        return val;
      }, []);
      return {
        category: c,
        parts: parts,
      }
    })
  }

  static getSentences() {
    return this.getGrammerData().reduce((val:sentenceType[], cur) => {
      const w:sentenceType = {
        id: cur.id,
        category: cur.category,
        part: cur.part,
        sentence: cur.sentence, 
        sentenceTrans: cur.sentenceTrans,
        dummyWord: cur.dummyWord,
      };

      if (!val.some(v => 
        v.part === w.part && v.sentence === w.sentence)) {
        val.push(w);
      }
      return val;
    }, []);  
  }
}
