import React, { useState, useEffect } from 'react';
import { Container, List, ListItem, ListItemText, Slider } from '@material-ui/core';
import Paginate from '../components/Pagination';
import Grammer from '../components/GrammerPage/Grammer';
import SideMenu from '../components/SideMenu';
import GrammerData from '../context/GrammerData';
import EnglishVoice from '../lib/EnglishVoice';
import { useSwipeable } from 'react-swipeable';

const GrammerPage:React.FC = () => {
  const firstPart = localStorage.getItem('grammerCurrentPart') ?? GrammerData.getFirstPart();
  const wIndex: number = localStorage.getItem('grammerWordIndex') ? Number(localStorage.getItem('grammerWordIndex')) : 0;
  const [slides, setSlides] = useState(GrammerData.getSentences().filter(w => w.part === firstPart));
  const [wordIndex, setWordIndex] = useState<number>(wIndex);
  const [currentPart, setCurrentPart] = useState<string>('');
  const [answer, setAnswer] = useState<string[]>([]);
  const [isAnsComplated, setIsAnsComplated] = useState<boolean>(false);
  const [stockWords, setStockWords] = useState<string[]>([]);
  const [ansWords, setAnsWords] = useState<string[]>([]);
  const [speechSpeedRate, setSpeechSpeedRate] = useState<number>(0.8);
  const [timer, setTimer] = useState<number>(0);

  const slidePrev = () => {
    if (wordIndex > 0) {
      const i:number = wordIndex - 1;
      const s:string[] = separateSentence(slides[i].sentence);
      setWordIndex(i);
      localStorage.setItem('grammerWordIndex', i.toString());
      setAnswer(s);
      const w:string[] = s.slice();
      w.push(slides[i].dummyWord.toLocaleLowerCase());
      setStockWords(shuffle(w));
      setAnsWords([]);
      setIsAnsComplated(false);
      setTimer(0);
    }
  }

  const slideNext = () => {
    if (wordIndex < slides.length-1) {
      const i:number = wordIndex + 1;
      const s:string[] = separateSentence(slides[i].sentence);
      setWordIndex(i);
      localStorage.setItem('grammerWordIndex', i.toString());
      setAnswer(s);
      const w:string[] = s.slice();
      w.push(slides[i].dummyWord.toLocaleLowerCase());
      setStockWords(shuffle(w));
      setAnsWords([]);
      setIsAnsComplated(false);
      setTimer(0);
    }
  }

  const changeCurrentPart = (val: string) => {
    if (currentPart !== val) {
      let s:any = [];
      if (val === 'favorite') {
        s = GrammerData.getFavoriteSentenceData();
      } else {
        s = GrammerData.getSentences().filter(w => w.part === val);
      }
      setSlides(s)
      setCurrentPart(val);
      localStorage.setItem('grammerCurrentPart', val);
      setWordIndex(0);
      localStorage.setItem('grammerWordIndex', '0');

      const a:string[] = s.length > 0 ? separateSentence(s[0].sentence) : [];
      setAnswer(a);
      const w:string[] = a.slice();
      w.push(s.length > 0 ? s[0].dummyWord.toLocaleLowerCase(): '');
      setStockWords(shuffle(w));
      setAnsWords([]);
      setIsAnsComplated(false);
      setTimer(0);
    }
  }

  const changeSpeechSpeedRate = (val: number) => {
    setSpeechSpeedRate(val);
  }

  const moveAnsWords = (word: string, index: number) => {
    const aw = ansWords;
    const sw = stockWords.filter((v, i) => {
      return i !== index;
    });
    aw.push(word);

    setAnsWords(aw);
    setStockWords(sw);

    EnglishVoice.speech(word, 'en-US', 1);
    if (answer.length === aw.length && answer.every((a, index) => a === aw[index])) {
      setIsAnsComplated(true);
      EnglishVoice.speech('OK. ' + slides[wordIndex].sentence, 'en-US', speechSpeedRate);
      console.log('OK. ' + slides[wordIndex].sentence);
    } else {
      setIsAnsComplated(false);
    }
  }

  const moveStockWords = (word: string, index: number) => {
    const sw = stockWords;
    const aw = ansWords.filter((v, i) => {
      return i !== index;
    });
    sw.push(word);

    setStockWords(sw);
    setAnsWords(aw);
    if (answer.length === aw.length && answer.every((a, index) => a === aw[index])) {
      setIsAnsComplated(true);
      EnglishVoice.speech('OK. ' + slides[wordIndex].sentence, 'en-US', speechSpeedRate);
    } else {
      setIsAnsComplated(false);
    }
  }

  const separateSentence = (s:string) => {
    return s.toLocaleLowerCase().slice(0, -1).split('  ');
  }

  const shuffle = (w:string[]) => {
    return w.map(function (a) { return { weight: Math.random(), value: a } })
    .sort(function (a, b) { return a.weight - b.weight })
    .map(function (a) { return a.value })
  }

  const swipeHandles = useSwipeable({
    onSwipedLeft: (e) => {
      if (e.absX >= 140 ) {
        slideNext();
      }
    },
    onSwipedRight: (e) => {
      if (e.absX >= 140) {
        slidePrev();
      }
    },
    preventDefaultTouchmoveEvent: true,
    trackMouse: true
  });

  useEffect(() => {
    changeCurrentPart(firstPart);
    const id = setInterval(() => {
      setTimer(t => t + 1);
    }, 1000);
    return () => clearInterval(id);
  }, []);

  return (
    <>
      {
        currentPart !== ''
        &&
        <SideMenu
          categories={GrammerData.getCategories()}
          parts={GrammerData.getParts()}
          currentPart={currentPart}
          changeCurrentPart={(val) => changeCurrentPart(val)}
          speechSpeedRate={speechSpeedRate}
          changeSpeechSpeedRate={(val) => changeSpeechSpeedRate(val)}
        />
      }
      <Container>
        <Paginate
          index={wordIndex}
          maxPage={slides.length}
          onClickPrev={slidePrev}
          onClickNext={slideNext}
        />
        <hr/>
        {
          slides.length == 0
          &&
          <p>no data</p>
        }
        {
          slides.length > 0
          &&
          <>
            <div {...swipeHandles}>
              <Grammer
                id={slides[wordIndex].id}
                currentPart={currentPart}
                answer={answer}
                stockWords={stockWords}
                ansWords={ansWords}
                sentence={slides[wordIndex].sentence}
                translate={slides[wordIndex].sentenceTrans}
                speechSpeedRate={speechSpeedRate}
                isAnsComplated={isAnsComplated}
                moveAnsWords={(w, i) => moveAnsWords(w, i)}
                moveStockWords={(w, i) => moveStockWords(w, i)}
              />
            </div>
            <List component="nav" aria-label="thirdry speechSpeed">
            <ListItem>
              <ListItemText primary="voice speed"/>
                <Slider
                  min={0.4}
                  max={1}
                  step={0.1}
                  value={speechSpeedRate}
                  aria-labelledby="speech-speed-rate"
                  valueLabelDisplay="auto"
                  onChange={(e, v) => {
                    let rate:number = speechSpeedRate;
                    if (typeof(v) ===  'number') {
                      rate = v;
                    }
                    setSpeechSpeedRate(rate);
                  }}
                />
              </ListItem>
            </List>
          </>
        }
        {
          timer >= 20
          &&
          <p>time over</p>
        }
        {
          timer < 20
          &&
          <p>Time: {20-timer}</p>
        }
      </Container>
    </>
  );
}

export default GrammerPage;