import React, { useRef, useEffect } from 'react';
import { Container, Button, Grid, List, ListItem, ListItemText, Slider, Typography, TextField } from '@material-ui/core';
import TranslateIcon from '@material-ui/icons/Translate';
import VolumeUpIcon from '@material-ui/icons/VolumeUp';
import Favorite from '../Common/Favorite';
import EnglishVoice from '../../lib/EnglishVoice';

interface GrammerProps {
  id: string;
  currentPart: string;
  sentence: string;
  translate: string;
  inputString: string;
  speechSpeedRate: number;
  isAnsComplated: boolean;
  changeInputString: (val:string) => void;
  changeIsAnsComplated: (val:boolean) => void;
}

const Grammer: React.FC<GrammerProps> = props => {
  const inputElement = useRef<HTMLElement>(null!) ;

  return (
    <>
      <Container maxWidth="sm">
        <Grid container>
          <Typography variant='subtitle1'>
            {props.translate}
          </Typography>
          <br/>
        </Grid>
      </Container>
      <Container maxWidth="sm">
        <Grid
          container
          direction="row"
          justify="space-around"
          alignItems="flex-end"
        >
          <Grid container direction="row">
            <TextField
              inputRef={inputElement}
              error={!props.isAnsComplated}
              label= {props.isAnsComplated ? "OK" : "input word"}
              fullWidth={true}
              value={props.inputString}
              variant="outlined"
              onChange={async (o) => 
                {
                  props.changeInputString(o.target.value);
                  if (props.sentence === o.target.value) {
                    await EnglishVoice.speech('OK. ' + props.sentence, 'en-US', props.speechSpeedRate);
                    await props.changeIsAnsComplated(true);
                  } else {
                    props.changeIsAnsComplated(false);
                  }
                }
              }
            />
          </Grid>
        </Grid>
        <hr/>
        <Grid
          container
          direction="row"
          spacing={5}
          justify="center"
          alignItems="center"
        >
          <Grid item xs={4}>
            <Favorite 
              category='GrammerWriting'
              part={props.currentPart}
              id={props.id}
            />
          </Grid>
        </Grid>
      </Container>
    </>
  );
}

export default Grammer;